// Colours
$primary: #FF6060;
$secondary: #F9F9FC;
$tertiary: #C4C4C4;
$backgroundLight: #F9F9FC;
$backgroundGallery: #F6F6F6;
$backgroundDark: #000000;
$shadow: rgba(0, 0, 0, 0.25);

// Typography
$fontWeightTitle: 700;
$fontWeightNormal: 500;
$fontMontserrat: 'Montserrat', Arial, Helvetica, sans-serif;
$fontMSourceCodePro: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;

/// Break points
$breakpoints: (
        'small': (min-width: 768px),
        'medium': (min-width: 1024px),
        'large': (min-width: 1440px),
);

// Sélectionner le format 'large'
$large_breakpoint: map-get($breakpoints, 'large');

// Récupérer la valeur de 'large'
$large_value: map-get($large_breakpoint, min-width);

$defaultSmallRadius: .5em;
$defaultMediumRadius: 1.5em;
$defaultHighRadius: 2.5em;
